import React, { useState } from "react";
import { FcCalendar } from "react-icons/fc";
const BookItem = (props) => {
  //console.log(props)

  const [isOpened, setIsOpened] = useState(false);

  function toggle() {
    setIsOpened((wasOpened) => !wasOpened);
  }

  return (
    <div className="list-group">
      <div className="row">
        <div className="col-md-2 col-sm-2 col-xs-3">{props.listKey}</div>
        <div className="col-md-2 col-sm-2">{props.list.title}</div>
        <div className="col-md-3 col-sm-2">{props.list.author_name}</div>
        <div className="col-md-3 col-sm-2">{props.list.cover_edition_key}</div>
        <FcCalendar size="45" onClick={toggle} />
      </div>
      {isOpened && (
        <div className="row p-4">
          <div className="col">
            <div className="card">
              <div className="card-header">Published Dates of the Book</div>
              <div className="card-body">
                {props.list.publish_date.map((date, i) => (
                  <ul className="list-group">
                    <li className="list-group-item">
                      #{i + 1} - {date}
                    </li>
                  </ul>
                ))}
                <div className="mx-auto btn btn-primary mt-4" onClick={toggle}>
                  Close
                </div>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default BookItem;
