import "./App.css";
import TextBox from "./components/TextBox";
import ButtonComponent from "./components/Button";
import Header from "./components/Header";
import { GetCompanyApi } from "./service/service";
import { useEffect, useState } from "react";
import BookItem from "./components/BookItem";
import { FcAlphabeticalSortingAz } from "react-icons/fc";

function App() {
  const [text, setText] = useState("");
  const [textBox, setTextBox] = useState("");
  const [suggestion, setSuggestion] = useState();
  const [detailedData, setDetailedData] = useState();
  const [isLoading, setIsLoading] = useState();
  const [sortType, setSortType] = useState("asc");

  useEffect(() => {
    const loadUsers = async (text) => {
      const response = await GetCompanyApi(text);
    };
    !detailedData && text && loadUsers(text);
  }, [text]);

  const onTextBoxChange = (textBoxText) => {
    setTextBox(textBoxText);
  };

  const onSearchData = async () => {
    if (textBox) {
      setIsLoading(true);
      const response = await GetCompanyApi(textBox);
      setSuggestion(response.data.docs);
      setIsLoading(false);
    }
  };

  const onSort = (key) => {
    setSuggestion([]);
    const data = [...suggestion].sort((a, b) => {
      const asc = sortType === "asc" ? true : false;
      if (a[key] && b[key]) {
        if (a[key][0] < b[key][0]) {
          return asc ? -1 : 1;
        } else if (a[key][0] > b[key][0]) {
          return asc ? 1 : -1;
        } else {
          return 0;
        }
      }
    });
    setSuggestion([...data]);

    setSortType(sortType === "desc" ? "asc" : "desc");
  };

  const onSuggestHandler = (text) => {
    setText(text.email);
    setDetailedData(text);
    setSuggestion([]);
  };

  const handleKeypress = (e) => {
    if (e.key === "Enter") {
      onSearchData();
    }
  };

  return (
    <>
      <div className="container mt-4">
        <Header name="Search Functionality Frontend Technical Task" />
        <div className="row">
          <div className="col-md-12 mb-4">
            <div className="card">
              <div className="card-body">
                <div className="md-form">
                  <TextBox
                    onChangeData={(e) => onTextBoxChange(e.target.value)}
                    value={textBox}
                    type="text"
                    placeholder="Search Book"
                    icon="fa fa-search prefix grey-text"
                    onKeyPressActon={(e) => handleKeypress(e)}
                  />

                  <ButtonComponent
                    onSearchData={() => onSearchData()}
                    text="Search"
                    type="button"
                  />
                  {isLoading && (
                    <div className="spinner-border" role="status">
                      <span className="visually-hidden">Loading...</span>
                    </div>
                  )}
                  {suggestion && !isLoading && (
                    <>
                      <div className="row font-weight-bold pb-3">
                        <div className="col-md-2 col-sm-2">#</div>
                        <div className="col">
                          <FcAlphabeticalSortingAz
                            size="30"
                            onClick={() => onSort("title")}
                          />{" "}
                          Title{" "}
                        </div>
                        <div className="col-md-3 col-sm-2">
                          <FcAlphabeticalSortingAz
                            size="30"
                            onClick={() => onSort("author_name")}
                          />
                          Author
                        </div>
                        <div className="col-md-3 col-sm-2">Cover Edition</div>
                        <div className="col-md-2 col-sm-2">Release Date</div>
                      </div>
                      {suggestion.length > 0 ? (
                        suggestion.map((suggestions, i) => (
                          <BookItem
                            list={suggestions}
                            listKey={i + 1}
                            key={i}
                            onClick={onSuggestHandler}
                          />
                        ))
                      ) : (
                        <div className="list-group">No Data Found </div>
                      )}
                    </>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default App;
